.card {
    position: relative;
    width: 30%;
    height: 50vh;
    margin-bottom: 10px;
    transform-style: preserve-3d;
    transform: rotateY(0deg);
    transition: transform 1s ease;
  }
  
  .card:hover {
    transform: rotateY(180deg);
  }
  
  .card .side {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    fonnnt: 24px;
    color: white;
    border-radius: 10px;
  }
  
  .card .front {
    background-color: #3498db;
  }
  
  .card .back {
    background-color: #e74c3c;
    transform: rotateY(180deg);
  }
  
