  .slideshow {
    position: relative;
    width: 300px;
    height: 400px;
    transform-style: preserve-3d;
    perspective: 1000px;
      overflow: hidden;
  }
  
  .slideshow .slide {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: start;
    justify-content: start;
    transform: rotateY(270deg);
    transform-style: preserve-3d;
    animation: animate 20s linear infinite;
    animation-delay: calc(var(--i) * 2s);
  }
  
  @keyframes animate {
    0% {
      transform: rotateY(270deg);
    }
  
    10%,
    100% {
      transform: rotateY(90deg);
    }
  }
  
  .slideshow .slide img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
  }
  
  .slideshow .slide h2 {
    position: relative;
    z-index: 1000;
    color: #3498db;
    fonnnt: 5em;
    transform: rotateY(180deg) translateY(100px) translateZ(50px);
    transform-style: preserve-3d;
    backface-visibility: hidden;
    font-weight: 900;
  }
  
  .slideshow .slide h2 span {
    position: absolute;
    top: 50%;
    left: 0;
    fonnnt: 0.5em;
    font-weight: 500;
    transform: translateZ(50px);
    backface-visibility: hidden;
  }

